/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Grill It (Like A Polaroid Picture)","date":"14 Jun 2004","slug":"/2004-06-14-grill-it-like-a-polaroid-picture/","tags":"Y2004"},"html":"<p>I've been preoccupied this weekend by my wife's birthday celebration, work matters, hay fever, and Euro 2004, but I would be remiss not to mention <a href=\"http://wiki.blogg.de/BlogGrill\">BlogGrill in Hamburg</a> last Thursday. I was there. I did man the grill for a time (after expressing doubt about the grilling skill of urban Germans and the equipment used). I did not introduce myself around, and thus missed half the people who were there. Next time we need name tags, maybe stickers that say \"Hi, my URL is....\".</p>\n<p>The main impression I gained is that an amazing number of north German bloggers once worked for McDonald's.</p>"},"next":{"fields":{"title":"Zidane is God!","date":"13 Jun 2004","slug":"/2004-06-13-zidane-is-god/"}},"previous":{"fields":{"title":"Pardon Me?","date":"15 Jun 2004","slug":"/2004-06-15-pardon-me/"}}},{"node":{"fields":{"title":"Zidane is God!","date":"13 Jun 2004","slug":"/2004-06-13-zidane-is-god/","tags":"Y2004"},"html":"<p><a href=\"http://www.spiegel.de/sport/fussball/0,1518,304030,00.html\">France 2:1 England</a> Zidane (90+1), Zidane (90+3)</p>\n<p><strong>Update:</strong> <a href=\"http://www.hebig.org/blogs/archives/main/001523.php\" title=\"God :: hebig.org/blog\">Haiko</a> and I did <em>not</em> coordinate our blog entries on this match, even though it may appear that we did.</p>"},"next":{"fields":{"title":"Sherpas for WordPress","date":"13 Jun 2004","slug":"/2004-06-13-sherpas-for-wordpress/"}},"previous":{"fields":{"title":"Grill It (Like A Polaroid Picture)","date":"14 Jun 2004","slug":"/2004-06-14-grill-it-like-a-polaroid-picture/"}}},{"node":{"fields":{"title":"Sherpas for WordPress","date":"13 Jun 2004","slug":"/2004-06-13-sherpas-for-wordpress/","tags":"Y2004"},"html":"<p><a href=\"http://weblog.burningbird.net/archives/2004/06/11/wordpress-and-bugs/\">Shelley</a> had a bad experience with the new WordPress bug tracker <a href=\"http://mosquito.wordpress.org/\">Mosquito</a>, and <a href=\"http://photomatt.net/archives/2004/06/11/mosquito-bites/\">Matt</a> (among others) thought she was being a poor sport about it. Nonetheless, Shelley does have a good point. To be successful, WordPress has to provide support to users that is both effective and understandable. </p>\n<p>Bug trackers are a subject near and dear to my heart, since maintaining them is one of my main responsibilities at work (using <a href=\"http://www.mantisbt.org/\">Mantis</a>, the same software behind Mosquito). My bug trackers at work take a relatively closed approach. The customer communicates solely with a supporter, not directly with the developers. The support is able to solve most problems on his own. If he needs help, his communication with the developers in the tracker are flagged as \"private\", and are invisible to the customer. We thus use Mantis as both a support tool and a development tool, but the two functions are kept separate. </p>\n<p>The supporter is the main link in the process. He acts as a <a href=\"http://en.wikipedia.org/wiki/Sherpa\">sherpa</a> between the customer and the developers, and decides whether the issue is a customer-side problem that he can help the customer solve, or a software problem that requires a developer (or a feature request that needs to be negotiated into the software spec). The customer doesn't really care what kind of problem he has, he simply wants it solved.</p>\n<p>I'm not sure if this model can be applied to an open source project. Sherpas for open source are few and far between. But without them, an open-to-all bug tracker can disintegrate into chaos. Support for WordPress is probably best given in a forum rather than a bug tracker, with real people and plain language. Technical projects like Samba or FreeBSD can take a 'apply the diff and see if it works' approach, but WordPress probably needs to act a little more commercial to serve its users well.</p>"},"next":{"fields":{"title":"Euro Election","date":"13 Jun 2004","slug":"/2004-06-13-euro-election/"}},"previous":{"fields":{"title":"Zidane is God!","date":"13 Jun 2004","slug":"/2004-06-13-zidane-is-god/"}}}],"pathPrefix":"","first":false,"last":false,"index":466,"pageCount":1101}}

/***/ })

